// eslint-disable-next-line
import { Link } from "react-router-dom";
import { FontAwesomeIcon as Icon } from "@fortawesome/react-fontawesome";
import { faDiscord } from "@fortawesome/free-brands-svg-icons";
import { faBars } from "@fortawesome/free-solid-svg-icons";
import Logo from "../util/Logo-1";

export default function Header() {
    return (
        <header className="Header-1A">
            <nav className="navbar navbar-expand-lg">
                <a href="/" className="navbar-brand" title="Xore" >
                    <Logo />
                </a>
                <div className="header-nav-divider" />
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbar-content" aria-controls="navbar-content" aria-expanded="false" aria-label="Toggle navigation">
                    <Icon icon={faBars} style={{ color: "white" }} />
                </button>
                <div className="collapse navbar-collapse" id="navbar-content">
                    <ul className="navbar-nav w-100" style={{ alignItems: "center" }}>
                        <li className="nav-item">
                            <a className="nav-link" href="/support">Support</a>
                        </li>
                        <li className="nav-item ml-auto">
                            <a href="/invite">
                                <button className="btn btn-imp">Add to Discord</button>
                            </a>
                        </li>
                        <li className="nav-item ml-3">
                            <a href="/login">
                                <button className="btn btn-login"><Icon icon={faDiscord} /> Login with Discord</button>
                            </a>
                        </li>
                    </ul>
                </div>
            </nav>
        </header>
    )
} //#F35353