import Site from "../components/Site";
import Feature from "../components/util/Feature";
import logo from "../assets/logos/xore-transparent.png"
import { Link } from 'react-router-dom';

export default function Home() {
    return (
        <Site>
            <div style={{ color: "transparent", width: 100 + "%", height: 10 + "%", zIndex: 200, position: 'absolute' }} />
            <img src={logo} alt="Xore Logo" width="82" />
            <h2>
                Xore
            </h2>
            <p>
                Xore is the only Discord bot you will ever need.
            </p>
            <p>
                Highly worked on and fully customizable.
            </p>
            <h2 id="features">Features</h2>
            <div id="features-section">
                <div className="d-inline-block">
                    <Feature icon="cog" header="Moderation" text="Warnings, kick & banning, role giving/removing, full customization" />
                </div>
                <div className="d-inline-block">
                    <Feature img-src="https://s.dyno.gg/web-assets/landing/card3.png" img-alt="Settings Icon" header="Moderation" text="Warnings, kick & banning, role giving/removing, full customization" />
                </div>
            </div>
            <h2 id="add-it">So, what are you waiting for?</h2>
            <Link to="/invite">
                <button className="btn btn-imp">Add Xore!</button>
            </Link>
        </Site>
    )
}