import { FontAwesomeIcon as Icon } from "@fortawesome/react-fontawesome";
import { faCog } from "@fortawesome/free-solid-svg-icons";

/**
 * @param {FeaturePropsWhatever} props 
 */
export default function Feature(props) {
    return (
        <div className={"card bg-dark mb-3 mr-4 d-inline-block"} style={{ width: 70 + "%" }}>
            <div className="card-body text-center">
                <Icon icon={props.icon ? props.icon.toLowerCase() === "cog" ? faCog : null : null } style={{ width: "2rem", height: "2rem" }} />
                <div className="text-left">
                    <h3>{props.header}</h3>
                    <p style={{ fontSize: .75 + "rem" }}>
                        {props.text}
                    </p>
                </div>
            </div>
        </div>
    )
};

/**
 * @typedef {object} FeaturePropsWhatever
 * @property {string} header
 * @property {string} text
 * @property {string} icon
 */