import Site from "../components/Site";

export default function PrivacyPolicy() {
    return (
        <Site>
            <h1>Privacy Policy</h1>
            <h2>What data we collect</h2>
            <p>
                By using Xore, you agree to allow us to store the following info whether you have read the privacy policy or not:
                <ul>
                    <li>
                        User IDs
                    </li>
                    <li>
                        Guild Settings
                    </li>
                </ul>
            </p>
        </Site>
    )
}