import { Link } from "react-router-dom";
import Row from "../util/Row";
import XoreTransparent from "../../assets/logos/xore-transparent.png"

export default function Footer() {
    return (
        <footer className="pt-md-5 mt-5 footer-wrap" style={{ background: "#202123" }}>
            <Row className="m-0 mb-2">
                <div className="col">
                    <img src={XoreTransparent} alt="Xore Logo" width="64" />
                    <h2 style={{ fontWeight: "bold", letterSpacing: `${2}px` }}>
                        Xore
                    </h2>
                    <small className="text-muted">
                        &copy; 2020-2021 Xore Developments. All rights reserved.
                    </small>
                </div>
                <div className="col">
                    <h4>Bot</h4>
                    <ul className="list-unstyled text-muted">
                        <li className="text-muted">
                            <Link className="text-muted" to="/invite">Invite</Link>
                        </li>
                        <li className="text-muted">
                            <Link className="text-muted" to="/support">Support</Link>
                        </li>
                    </ul>
                </div>
                <div className="col">
                    <h4>Bot</h4>
                    <ul className="list-unstyled text-muted">
                        <li className="text-muted">
                            <Link className="text-muted" to="/invite">Invite</Link>
                        </li>
                        <li className="text-muted">
                            <Link className="text-muted" to="/support">Support</Link>
                        </li>
                    </ul>
                </div>
            </Row>
        </footer>
    )
}